import React, { useEffect, useState } from 'react';
import { kebabCase } from 'lodash';
import { useRouter } from 'next/router';
import { sendApiRequest, readFile } from 'lib/api/public';
import { Box, TextField, Button, Typography, LinearProgress, Avatar } from '@mui/material';
import notify from 'lib/notify';
import { getUrlApp } from 'lib/api/public';
import { renderField } from '../../components/utils';
import Uploader from '../../components/common/Uploader';

const host = getUrlApp().replace('https://', '').replace('http://', '').replace('www.', '');

const OrganizationSettingsForm = ({ requireSubdomain = false }: { requireSubdomain?: boolean }) => {
    const router = useRouter();

    const [isLoading, setIsLoading] = useState(true);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [organization, setOrganization] = useState(null);
    const [subdomain, setSubdomain] = useState(null);
    const [isSubdomainAvailable, setIsSubdomainAvailable] = useState(true);

    const fetchOrganization = async () => {
        try {
            const response = await sendApiRequest({
                path: '/organization',
                method: 'GET',
            });

            setOrganization(response);
            setSubdomain(response.subdomain);

            setIsLoading(false);
        } catch (error) {
            setIsLoading(false);
        }
    };

    const checkSubdomainAvailability = async (value) => {
        try {
            const response = await sendApiRequest({
                path: `/organization/check-subdomain`,
                method: 'POST',
                data: {
                    subdomain: value,
                },
            });

            setIsSubdomainAvailable(response);
        } catch (error) {
            console.error(error);
        }
    }

    useEffect(() => {
        fetchOrganization();
    }, []);

    const onChange = (key, value) => {
        setOrganization({ ...organization, [key]: value });
    };

    const onChangeName = (e) => {
        const name = e.target.value;
        setOrganization({ ...organization, name: name });
    }

    const onBlurName = (e) => {
        const name = e.target.value;
        
        if (!subdomain) {
            const newSubdomain = kebabCase(name);
            setSubdomain(newSubdomain);
            setSubdomain(newSubdomain); checkSubdomainAvailability(newSubdomain);
        }
    }

    const onSubdomainChange = (e) => {
        const subdomain = e.target.value;

        setSubdomain(subdomain);
        checkSubdomainAvailability(subdomain);
    }

    const onSubmit = async () => {
        if (!isSubdomainAvailable) {
            notify('Subdomain is not available');
            return;
        }

        if (!organization.name) {
            notify('Name is required');
            return;
        }

        if (requireSubdomain && !subdomain) {
            notify('Subdomain is required');
            return;
        }

        try {
            setIsSubmitting(true);

            await sendApiRequest({
                path: '/organization',
                method: 'POST',
                data: {
                    name: organization.name,
                    website: organization.website,
                    subdomain: isSubdomainAvailable ? subdomain : null,
                    logo: organization.logo,
                },
            });

            setIsSubmitting(false);

            if (subdomain) {
                notify('You will be logged out and redirected to the new subdomain.');

                setTimeout(() => {
                    router.push(`/auth/logout?subdomain=${subdomain}`);
                }, 3000);
            } else {
                notify('Organization updated successfully');
            }
        } catch (error) {
            setIsSubmitting(false);
            notify(error.message);
        }
    };

    if (isLoading || !organization) return <LinearProgress />;

    return (
        <>
            {renderField({
                label: 'Name',
                required: true,
                renderControl: () => <TextField fullWidth value={organization.name} onChange={onChangeName} onBlur={onBlurName} />,
            })}

            {renderField({
                label: 'Subdomain',
                required: requireSubdomain,
                renderControl: () => {
                    return (
                        <>
                            <TextField fullWidth onChange={onSubdomainChange} value={subdomain} />
                            <Typography variant='caption'>{`https://${subdomain || ''}${subdomain ? '.' : ''}${host}`}</Typography>

                            <Box>
                                {!isSubdomainAvailable && <Typography variant='caption' color='red'>Subdomain is not available</Typography>}
                            </Box>
                        </>
                    )
                }
            })}

            {renderField({
                label: 'Website',
                renderControl: () => <TextField fullWidth onChange={(e) => onChange('website', e.target.value)} value={organization?.website} />,
            })}

            {renderField({
                label: 'Logo', renderControl: () => {
                    const logo = organization.logo;

                    return (
                        <>
                            { logo &&

                            (
                                <>
                                    <Avatar src={readFile(logo)} alt="Logo" />
                                    <br />
                                    <br />
                                </>
                            )}

                            <Uploader
                                label="Upload"
                                multiple={false}
                                defaultFiles={logo ? [{ name: logo, url: logo }] : []} onChange={(result) => {

                                    if (result) {
                                        setOrganization({ ...organization, logo: result.url });
                                    } else {
                                        setOrganization({ ...organization, logo: null });
                                    }
                                }} />
                        </>
                    );
                }
            })}

            <Button style={{ float: 'right', marginTop: '-20px' }} color='primary' variant='contained' onClick={onSubmit} disabled={isLoading || !isSubdomainAvailable || isSubmitting}>
                {isSubmitting ? 'Saving...' : 'Save'}
            </Button>
        </>
    )
};

export default OrganizationSettingsForm;